"use strict";
var script = document.currentScript || (function () {
    var scripts = document.getElementsByTagName("script");
    return scripts[scripts.length - 1];
})();
var q = new URL(script.getAttribute("src") || "", location.href).searchParams;
window.addEventListener("keydown", function (ev) {
    if (ev.key === (q.get("key") || "s")) {
        var s = window.getSelection().toString();
        if (s) {
            speak(s);
        }
    }
});
function speak(s) {
    var lang = q.get("lang") || "zh-CN";
    var rate = parseInt(q.get("rate") || "") || (lang === "zh-CN" ? 0.8 : 1);
    var allVoices = speechSynthesis.getVoices();
    var vs = allVoices.filter(function (v) { return v.lang === lang; });
    if (vs.length === 0) {
        var m1 = lang.substr(0, 2);
        var m2 = lang.substr(3, 2);
        var r1_1 = new RegExp("^" + m1 + "[-_]" + m2, "i");
        vs = allVoices.filter(function (v) { return r1_1.test(v.lang); });
        if (vs.length === 0) {
            var r2_1 = new RegExp("^" + m1, "i");
            vs = allVoices.filter(function (v) { return r2_1.test(v.lang); });
        }
    }
    if (vs.length > 0) {
        var u = new SpeechSynthesisUtterance(s);
        u.lang = vs[0].lang;
        u.rate = rate || 0.8;
        speechSynthesis.speak(u);
    }
}
